import React from 'react';
import { connect } from 'react-redux';
import { userFutureEdit } from '../../../redux/user/user.actions';

import { ReactComponent as Close} from '../../../svg/close-outline.svg';
import Button from '../../button/button.component';
import { replaceComma, checkNumberField } from '../../../utils/form.funtions';


const FuturesUpdateRecordForm = ({ setWalletTrackerLabel, hideForm, userFutureEdit, setFuturesTableLoading, editData, setEditData }) => {

    const { typeOfFuture, contract, profit, date, entry, laverage } = editData;
    
    const handleChange = e => {
        const { name, value } = e.target;

        setEditData(prevState => ({
            ...prevState,
            [name]: value
        }))
    }

    const handleSubmit = e => {
        e.preventDefault();

        if (editData.profit) editData.profit = replaceComma(editData.profit);
        if (editData.entry) editData.entry = replaceComma(editData.entry);

            editData.typeOfFuture = editData.typeOfFuture.toLowerCase();
        if (editData.typeOfFuture !== 'short' && editData.typeOfFuture !== 'long') {
            return setWalletTrackerLabel({
                status: true,
                message: 'Provide only Long or Short type.'
            })
        }

        if (checkNumberField(editData.profit, editData.entry)) {
            return setWalletTrackerLabel({
                status: true,
                message: 'Provide only numbers in profit and entry fields. '
            })
        }

        setFuturesTableLoading();
        userFutureEdit(editData);
        hideForm();
    }
    
    return (
    <div className='walletTrackerFuturesForm'>
        <div className='walletTrackerFuturesForm__container'>
            <form onSubmit={handleSubmit} >
            <div className='close'>
                <Close className='close--icon' onClick={hideForm} />
            </div>
            <h1 className='walletTrackerFuturesForm__header'>Update Record</h1>
            <div className='walletTrackerFuturesForm__box'>
                <div className='walletTrackerFuturesForm__inputBox'>
                    <label htmlFor='walletTrackerFuturesForm__type'>TYPE: </label>
                    <input defaultValue={typeOfFuture} onChange={handleChange} list='shortAndLong' required name='typeOfFuture' type='text' placeholder='type' id='walletTrackerFuturesForm__type' />
                <datalist id="shortAndLong">
                    <option value="Short" />
                    <option value="Long" />
                </datalist>
                </div>
                <div className='walletTrackerFuturesForm__inputBox'>
                    <label htmlFor='walletTrackerFuturesForm__contract'>CONTRACT: </label>
                    <input defaultValue={contract} onChange={handleChange} type='text' name='contract' placeholder='contract' id='walletTrackerFuturesForm__contract' />
                </div>
                <div className='walletTrackerFuturesForm__inputBox'>
                    <label htmlFor='walletTrackerFuturesForm__entry'>ENTRY: </label>
                    <input required defaultValue={entry} onChange={handleChange} name='entry' type='text' placeholder='entry' id='walletTrackerFuturesForm__entry' />
                </div>
                <div className='walletTrackerFuturesForm__inputBox'>
                    <label htmlFor='walletTrackerFuturesForm__laverage'>LAVERAGE: </label>
                    <input defaultValue={laverage} onChange={handleChange} name='laverage' type='text' placeholder='laverage' id='walletTrackerFuturesForm__laverage' />
                </div>
                <div className='walletTrackerFuturesForm__inputBox'>
                    <label htmlFor='walletTrackerFuturesForm__profit'>PROFIT: </label>
                    <input defaultValue={profit} onChange={handleChange} name='profit' type='text' placeholder='profit' id='walletTrackerFuturesForm__profit' />
                </div>
                <div className='walletTrackerFuturesForm__inputBox'>
                    <label htmlFor='walletTrackerFuturesForm__date'>DATE: </label>
                    <input defaultValue={date} onChange={handleChange} required name='date' type='date' placeholder='date' id='walletTrackerFuturesForm__date' />
                </div>
                <Button type='submit' styles={{marginTop: 0}} text='APPLY' />
            </div>
            
            </form>
        </div>
    </div>
)};

const mapDispatchToProps = dispatch => ({
    userFutureEdit: data => dispatch(userFutureEdit(data))
});

export default connect(null, mapDispatchToProps)(FuturesUpdateRecordForm);